n                            </div>\n                            <div class=\"font-medium text-sm text-gray-100 w-full\">\n                                <Link @click=\"videoPlayer.makeVideoTopRight()\"\n                                      :href=\"route('profile.show')\"\n                                      :active=\"route().current('profile.show')\">\n                                    {{ $page.props.user.email }}\n                                </Link>\n                            </div>\n                        </div>\n                    </div>\n                    <div class=\"justify-end text-right w-12\">\n                        <div v-if=\"$page.props.user.role_id === 2\" class=\"text-xs font-semibold text-fuchsia-700\">PREMIUM SUBSCRIBER</div>\n                        <div v-if=\"$page.props.user.role_id === 3\" class=\"text-xs font-semibold text-fuchsia-700\">VIP</div>\n                        <div v-if=\"$page.props.user.role_id === 4\" class=\"text-xs font-semibold text-fuchsia-700\">CREATOR</div>\n                        <div v-if=\"$page.props.user.isAdmin === 1\" class=\"text-xs font-semibold text-red-700\">ADMIN</div>\n                    </div>\n\n                </div>\n            </div>\n\n\n            <div class=\"space-y-1 z-50 bg-gray-900 pb-20 border-b border-1 border-white\">\n\n                <JetResponsiveNavLink\n                    v-if=\"$page.props.user.role_id === 1\"\n                    @click=\"userStore.closeNavDropdown()\"\n                    :href=\"route('upgrade')\"\n                    :active=\"route().current('upgrade')\"\n                >\n                    <div class=\"rounded-lg p-2 bg-gray-100 text-black hover:bg-gray-300 hover:text-green-900\">\n                        CLICK HERE TO UPGRADE YOUR ACCOUNT\n                    </div>\n                </JetResponsiveNavLink>\n\n                <JetResponsiveNavLink\n                    v-if=\"$page.props.user.role_id === 4\"\n                    @click=\"userStore.closeNavDropdown()\"\n                    :href=\"route('dashboard')\"\n                    :active=\"route().current('dashboard')\">\n                    Dashboard\n                </JetResponsiveNavLink>\n\n                <JetResponsiveNavLink\n                    v-if=\"$page.props.user.role_id === 3\"\n                    @click=\"userStore.closeNavDropdown()\"\n                    :href=\"route('library')\"\n                    :active=\"route().current('library')\">\n                    My Library\n                </JetResponsiveNavLink>\n\n                <JetResponsiveNavLink\n                    @click=\"userStore.closeNavDropdown()\"\n                    :href=\"route('stream')\"\n                    :active=\"route().current('stream')\">\n                        Stream\n                </JetResponsiveNavLink>\n\n                <JetResponsiveNavLink\n                    @click=\"userStore.closeNavDropdown()\"\n                    :href=\"route('schedule')\"\n                    :active=\"route().current('schedule')\">\n                        Schedule\n                </JetResponsiveNavLink>\n\n                <JetResponsiveNavLink\n                    v-if=\"$page.props.user.role_id === 2 || $page.props.user.role_id === 3 || $page.props.user.role_id === 4\"\n                    @click=\"userStore.closeNavDropdown()\"\n                    :href=\"route('news')\"\n                    :active=\"route().current('news')\">\n                        News\n                </JetResponsiveNavLink>\n\n                <JetResponsiveNavLink\n                    v-if=\"$page.props.user.role_id === 3 || $page.props.user.role_id === 4\"\n                    @click=\"userStore.closeNavDropdown()\"\n                    :href=\"route('movies')\"\n                    :active=\"route().current('movies')\">\n                        Movies\n                </JetResponsiveNavLink>\n\n                <JetResponsiveNavLink\n                    v-if=\"$page.props.user.role_id === 2 || $page.props.user.role_id === 3 || $page.props.user.role_id === 4\"\n                    @click=\"userStore.closeNavDropdown()\"\n                    :href=\"route('shows')\"\n                    :active=\"route().current('shows')\">\n                        Shows\n                </JetResponsiveNavLink>\n\n                <JetResponsiveNavLink\n                    @click=\"userStore.closeNavDropdown()\"\n                    :href=\"route('shop')\"\n                    :active=\"route().current('shop')\">\n                        Shop\n                </JetResponsiveNavLink>\n\n                <JetResponsiveNavLink\n                    @click=\"userStore.closeNavDropdown()\"\n                    :href=\"route('settings')\"\n                    :active=\"route().current('settings')\">\n                        Settings\n                </JetResponsiveNavLink>\n\n                <!-- Authentication -->\n                <form method=\"POST\" @submit.prevent=\"logout\">\n                    <JetResponsiveNavLink as=\"button\" class=\"border-t-0\">\n                        Log Out\n                    </JetResponsiveNavLink>\n                </form>\n\n\n\n<!--                &lt;!&ndash; Creator Only Links &ndash;&gt;-->\n<!--                <div v-if=\"$page.props.user.role_id === 4\">-->\n<!--                    <div class=\"border-t border-1 mt-3 border-gray-300 bg-indigo-900 block px-4 py-2 text-xs text-white\">-->\n<!--                        Creator Only Links-->\n<!--                    </div>-->\n\n\n\n<!--                </div>-->\n\n                <!-- Admin Only Links -->\n                    <div v-if=\"$page.props.user.isAdmin === 1\">\n                        <div class=\"border-t border-1 mt-3 border-gray-300 bg-black block px-4 py-2 text-xs text-gray-400\">\n                            Admin Only Links\n                        </div>\n\n                        <JetResponsiveNavLink\n                            @click=\"userStore.closeNavDropdown()\"\n                            :href=\"route('videoupload')\"\n                            :active=\"route().current('videoupload')\">\n                            Video Upload\n                        </JetResponsiveNavLink>\n\n                        <JetResponsiveNavLink\n                            @click=\"userStore.closeNavDropdown()\"\n                            :href=\"route('training')\"\n                            :active=\"route().current('training')\">\n                            Training\n                        </JetResponsiveNavLink>\n\n                        <JetResponsiveNavLink\n                            @click=\"userStore.closeNavDropdown()\"\n                            :href=\"route('video')\"\n                            :active=\"route().current('video')\">\n                            MistServer API\n                        </JetResponsiveNavLink>\n                    </div>\n\n\n\n\n                <div class=\"flex flex-col w-full space-y-1 text-gray-600 text-sm\">\n<!--                    <div class=\"flex pt-12 justify-center\">Web application concept and design</div>-->\n                    <div class=\"flex justify-center\">not<span class=\"text-xs\">&#174;</span>TV &#169;2010-{{new Date().getFullYear()}}</div>\n                    <div class=\"flex justify-center\">Beta Preview v0.5</div>\n<!--                    <div class=\"flex pt-4 justify-center\">Please send us comments and questions <a href=\"https://help.not.tv/\" target=\"_blank\" class=\"text-blue-600 hover:text-blue-40\">&nbsp; here</a>.</div>-->\n                </div>\n            </div>\n        </div> <div class=\"fixed w-full bottom-4 text-center hidden\">Scroll the menu.</div>\n    </div>\n\n</template>\n\n<script setup>\nimport JetResponsiveNavLink from '@/Jetstream/ResponsiveNavLink.vue'\nimport NotificationsButton from '@/Components/Navigation/NotificationsButton.vue'\nimport { useVideoPlayerStore } from \"@/Stores/VideoPlayerStore.js\"\nimport { useChatStore } from \"@/Stores/ChatStore\"\nimport { useUserStore } from \"@/Stores/UserStore\"\nimport { ref } from \"vue\"\nimport {Inertia} from \"@inertiajs/inertia\"\n\nconst showingNavigationDropdown = ref(false);\n\nlet videoPlayerStore = useVideoPlayerStore();\nlet chatStore = useChatStore();\nlet userStore = useUserStore();\n\nconst logout = () => {\n    videoPlayerStore.changeChannel('one');\n    videoPlayerStore.mute();\n    videoPlayerStore.fullPage = true;\n    videoPlayerStore.loggedIn = false;\n    videoPlayerStore.class = \"welcomeVideoClass\";\n    videoPlayerStore.videoContainerClass = \"welcomeVideoContainer\";\n    chatStore.class = \"chatHidden\";\n    chatStore.show = false;\n    Inertia.post(route('logout'));\n};\n\n</script>\n\n<style>\n.hamburgerMask {\n    z-index: 100;\n}\n</style>\n"],"sourceRoot":""}]);
